<template>
	<view class="container">
		<!-- ÊâìÂç∞Êú∫ÂàóË°® -->
		<view class="printer-list">
			<view 
				v-for="(printer, index) in printerList" 
				:key="index"
				class="printer-item"
				:class="{'selected': selectedPrinter && selectedPrinter.id === printer.id}"
				@click="selectPrinter(printer)"
			>
				<view class="printer-main">
					<view class="printer-icon">üñ®Ô∏è</view>
					<view class="printer-info">
						<text class="printer-name">{{ printer.name }}</text>
						<text class="printer-model">{{ printer.model }}</text>
					</view>
				</view>
				<view class="printer-status" :class="printer.status">
					<view class="status-dot"></view>
					<text>{{ printer.status === 'online' ? 'Âú®Á∫ø' : 'Á¶ªÁ∫ø' }}</text>
				</view>
			</view>
			
			<view class="empty-tip" v-if="printerList.length === 0">
				<text>ÊöÇÊó†ÊâìÂç∞Êú∫</text>
				<text class="sub-tip">ÁÇπÂáª‰∏ãÊñπÊåâÈíÆÊ∑ªÂä†ÊâìÂç∞Êú∫</text>
			</view>
		</view>
		
		<!-- Ê∑ªÂä†ÊâìÂç∞Êú∫ÊåâÈíÆ -->
		<view class="add-section">
			<button class="add-btn" @click="showAddDialog">+ Ê∑ªÂä†ÊâìÂç∞Êú∫</button>
		</view>
		
		<!-- Ê∑ªÂä†ÊâìÂç∞Êú∫ÂØπËØùÊ°Ü -->
		<view class="dialog-mask" v-if="showDialog" @click="closeDialog">
			<view class="dialog" @click.stop>
				<view class="dialog-title">Ê∑ªÂä†ÊâìÂç∞Êú∫</view>
				
				<view class="dialog-content">
					<view class="form-item">
						<text class="label">ÊâìÂç∞Êú∫ÂêçÁß∞</text>
						<input 
							v-model="newPrinter.name" 
							class="input"
							placeholder="ËØ∑ËæìÂÖ•ÊâìÂç∞Êú∫ÂêçÁß∞"
						/>
					</view>
					
					<view class="form-item">
						<text class="label">ÊâìÂç∞Êú∫ÂûãÂè∑</text>
						<input 
							v-model="newPrinter.model" 
							class="input"
							placeholder="ËØ∑ËæìÂÖ•ÊâìÂç∞Êú∫ÂûãÂè∑"
						/>
					</view>
					
					<view class="form-item">
						<text class="label">ÊâìÂç∞Êú∫ID</text>
						<view class="scan-row">
							<input 
								v-model="newPrinter.id" 
								class="input flex-input"
								placeholder="ËØ∑Êâ´ÊèèÊàñËæìÂÖ•ÊâìÂç∞Êú∫ID"
							/>
							<button class="scan-btn" @click="scanCode">Êâ´Á†Å</button>
						</view>
					</view>
					
					<view class="form-item">
						<text class="label">ÊâìÂç∞Êú∫ÂØÜÈí•</text>
						<input 
							v-model="newPrinter.secret" 
							class="input"
							placeholder="ËØ∑ËæìÂÖ•ÊâìÂç∞Êú∫ÂØÜÈí•"
						/>
					</view>
				</view>
				
				<view class="dialog-footer">
					<button class="cancel-btn" @click="closeDialog">ÂèñÊ∂à</button>
					<button class="confirm-btn" @click="addPrinter">Á°ÆÂÆö</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import printApi from '@/utils/printApi.js';
	
	export default {
		data() {
			return {
				printerList: [],
				selectedPrinter: null,
				showDialog: false,
				newPrinter: {
					name: '',
					model: '',
					id: '',
					secret: ''
				}
			}
		},
		
		onLoad() {
			this.loadPrinters();
		},
		
		onShow() {
			// ÊØèÊ¨°ÊòæÁ§∫Êó∂Âà∑Êñ∞ÊâìÂç∞Êú∫ÂàóË°®
			this.loadPrinters();
		},
		
		methods: {
			// Âä†ËΩΩÊâìÂç∞Êú∫ÂàóË°®
			loadPrinters() {
				// ‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩ
				const printers = uni.getStorageSync('printers') || [];
				this.printerList = printers;
				
				// Âä†ËΩΩÂΩìÂâçÈÄâ‰∏≠ÁöÑÊâìÂç∞Êú∫
				this.selectedPrinter = uni.getStorageSync('selectedPrinter');
				
				// Âà∑Êñ∞ÊâìÂç∞Êú∫Áä∂ÊÄÅ
				this.refreshPrinterStatus();
			},
			
			// Âà∑Êñ∞ÊâìÂç∞Êú∫Áä∂ÊÄÅ
			async refreshPrinterStatus() {
				for (let printer of this.printerList) {
					try {
						// Ë∞ÉÁî®APIËé∑ÂèñÊâìÂç∞Êú∫Áä∂ÊÄÅ
						const result = await printApi.getPrinterStatus(printer.id);
						printer.status = result.data.status;
					} catch (error) {
						// APIË∞ÉÁî®Â§±Ë¥•ÔºåÈªòËÆ§ËÆæÁΩÆ‰∏∫Âú®Á∫øÔºàÂõ†‰∏∫ÂêéÂè∞ÊòæÁ§∫Âú®Á∫øÔºâ
						printer.status = 'online';
					}
				}
				
				// ‰øùÂ≠òÊõ¥Êñ∞ÂêéÁöÑÊâìÂç∞Êú∫ÂàóË°®
				uni.setStorageSync('printers', this.printerList);
				
				// Êõ¥Êñ∞ÂΩìÂâçÈÄâ‰∏≠ÁöÑÊâìÂç∞Êú∫Áä∂ÊÄÅ
				if (this.selectedPrinter) {
					const currentPrinter = this.printerList.find(p => p.id === this.selectedPrinter.id);
					if (currentPrinter) {
						this.selectedPrinter.status = currentPrinter.status;
						uni.setStorageSync('selectedPrinter', this.selectedPrinter);
					}
				}
			},
			
			// ÈÄâÊã©ÊâìÂç∞Êú∫
			selectPrinter(printer) {
				this.selectedPrinter = printer;
				uni.setStorageSync('selectedPrinter', printer);
				
				uni.showToast({
					title: 'Â∑≤ÈÄâÊã©Ôºö' + printer.name,
					icon: 'success'
				});
			},
			
			// ÊòæÁ§∫Ê∑ªÂä†ÂØπËØùÊ°Ü
			showAddDialog() {
				this.showDialog = true;
			},
			
			// ÂÖ≥Èó≠ÂØπËØùÊ°Ü
			closeDialog() {
				this.showDialog = false;
				this.newPrinter = {
					name: '',
					model: '',
					id: '',
					secret: ''
				};
			},
			
			// Êâ´Á†Å
			scanCode() {
				uni.scanCode({
					success: (res) => {
						// Ëß£Êûê‰∫åÁª¥Á†ÅÂÜÖÂÆπ
						try {
							const data = JSON.parse(res.result);
							this.newPrinter.id = data.deviceId || data.printerId || res.result;
							this.newPrinter.secret = data.secret || data.password || '';
							this.newPrinter.name = data.name || this.newPrinter.name;
							this.newPrinter.model = data.model || this.newPrinter.model;
						} catch (error) {
							this.newPrinter.id = res.result;
						}
					}
				});
			},
			
			// Âø´ÈÄüÂ°´ÂÖÖÁ§∫‰æãËÆæÂ§á
			quickFillDevice() {
				const defaultDevice = printApi.getDefaultDevice();
				this.newPrinter.id = defaultDevice.id;
				this.newPrinter.name = 'ÊµãËØï‰∫ëÁõí - ' + defaultDevice.name;
				this.newPrinter.model = defaultDevice.model;
				this.newPrinter.secret = defaultDevice.password;
				uni.showToast({
					title: 'Â∑≤Â°´ÂÖÖËÆæÂ§á‰ø°ÊÅØ',
					icon: 'success'
				});
			},
			
			// Ê∑ªÂä†ÊâìÂç∞Êú∫
			async addPrinter() {
				// È™åËØÅË°®Âçï
				if (!this.newPrinter.name) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊâìÂç∞Êú∫ÂêçÁß∞',
						icon: 'none'
					});
					return;
				}
				
				if (!this.newPrinter.id) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊâìÂç∞Êú∫ID',
						icon: 'none'
					});
					return;
				}
				
				uni.showLoading({
					title: 'Ê∑ªÂä†‰∏≠...'
				});
				
				try {
					// Ë∞ÉÁî®APIÊ∑ªÂä†ÊâìÂç∞Êú∫
					const result = await printApi.addPrinter({
						name: this.newPrinter.name,
						model: this.newPrinter.model,
						printerId: this.newPrinter.id,
						secret: this.newPrinter.secret
					});
					
					// Ê∑ªÂä†Âà∞Êú¨Âú∞ÂàóË°®
					const printer = {
						...this.newPrinter,
						status: 'online'
					};
					
					this.printerList.push(printer);
					uni.setStorageSync('printers', this.printerList);
					
					// Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÂè∞ÊâìÂç∞Êú∫ÔºåËá™Âä®ÈÄâ‰∏≠
					if (this.printerList.length === 1) {
						this.selectPrinter(printer);
					}
					
					uni.hideLoading();
					uni.showToast({
						title: 'Ê∑ªÂä†ÊàêÂäü',
						icon: 'success'
					});
					
					this.closeDialog();
					
				} catch (error) {
					uni.hideLoading();
					uni.showToast({
						title: error.message || 'Ê∑ªÂä†Â§±Ë¥•',
						icon: 'none'
					});
				}
			},
			
			// Âà†Èô§ÊâìÂç∞Êú∫ÔºàÈïøÊåâÔºâ
			deletePrinter(printer) {
				uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ÊâìÂç∞Êú∫"' + printer.name + '"ÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							const index = this.printerList.findIndex(p => p.id === printer.id);
							if (index > -1) {
								this.printerList.splice(index, 1);
								uni.setStorageSync('printers', this.printerList);
								
								// Â¶ÇÊûúÂà†Èô§ÁöÑÊòØÂΩìÂâçÈÄâ‰∏≠ÁöÑÊâìÂç∞Êú∫ÔºåÊ∏ÖÁ©∫ÈÄâÊã©
								if (this.selectedPrinter && this.selectedPrinter.id === printer.id) {
									this.selectedPrinter = null;
									uni.removeStorageSync('selectedPrinter');
								}
								
								uni.showToast({
									title: 'Âà†Èô§ÊàêÂäü',
									icon: 'success'
								});
							}
						}
					}
				});
			}
		}
	}
</script>

<style scoped>
	.container {
		min-height: 100vh;
		background-color: #f5f5f5;
		padding: 20rpx;
	}
	
	/* ÊâìÂç∞Êú∫ÂàóË°® */
	.printer-list {
		margin-bottom: 20rpx;
	}
	
	.printer-item {
		background-color: #ffffff;
		border-radius: 20rpx;
		padding: 30rpx;
		margin-bottom: 20rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border: 2px solid transparent;
		transition: all 0.3s;
	}
	
	.printer-item.selected {
		border-color: #1890ff;
		background-color: #e6f7ff;
	}
	
	.printer-main {
		display: flex;
		align-items: center;
		flex: 1;
	}
	
	.printer-icon {
		font-size: 80rpx;
		margin-right: 30rpx;
	}
	
	.printer-info {
		display: flex;
		flex-direction: column;
	}
	
	.printer-name {
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
		margin-bottom: 10rpx;
	}
	
	.printer-model {
		font-size: 26rpx;
		color: #999999;
	}
	
	.printer-status {
		display: flex;
		align-items: center;
		padding: 10rpx 20rpx;
		border-radius: 20rpx;
		font-size: 24rpx;
	}
	
	.printer-status.online {
		background-color: #f6ffed;
		color: #52c41a;
	}
	
	.printer-status.offline {
		background-color: #fff1f0;
		color: #ff4d4f;
	}
	
	.status-dot {
		width: 12rpx;
		height: 12rpx;
		border-radius: 50%;
		margin-right: 10rpx;
	}
	
	.printer-status.online .status-dot {
		background-color: #52c41a;
	}
	
	.printer-status.offline .status-dot {
		background-color: #ff4d4f;
	}
	
	/* Ê∑ªÂä†ÊåâÈíÆ */
	.add-section {
		padding: 20rpx 0;
	}
	
	.add-btn {
		width: 100%;
		height: 88rpx;
		line-height: 88rpx;
		background-color: #1890ff;
		color: #ffffff;
		border: none;
		border-radius: 12rpx;
		font-size: 32rpx;
	}
	
	/* ÂØπËØùÊ°Ü */
	.dialog-mask {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, 0.5);
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 9999;
	}
	
	.dialog {
		width: 640rpx;
		background-color: #ffffff;
		border-radius: 20rpx;
		overflow: hidden;
	}
	
	.dialog-title {
		font-size: 36rpx;
		font-weight: bold;
		color: #333333;
		padding: 40rpx 30rpx 20rpx;
		text-align: center;
	}
	
	.dialog-content {
		padding: 20rpx 30rpx;
		max-height: 800rpx;
		overflow-y: auto;
	}
	
	.form-item {
		margin-bottom: 30rpx;
	}
	
	.label {
		display: block;
		font-size: 28rpx;
		color: #333333;
		margin-bottom: 15rpx;
	}
	
	.input {
		width: 100%;
		height: 80rpx;
		padding: 0 20rpx;
		border: 1px solid #d9d9d9;
		border-radius: 8rpx;
		font-size: 28rpx;
	}
	
	.scan-row {
		display: flex;
		align-items: center;
		gap: 20rpx;
	}
	
	.flex-input {
		flex: 1;
	}
	
	.scan-btn {
		width: 140rpx;
		height: 80rpx;
		line-height: 80rpx;
		background-color: #1890ff;
		color: #ffffff;
		border: none;
		border-radius: 8rpx;
		font-size: 28rpx;
		padding: 0;
	}
	
	.dialog-footer {
		display: flex;
		border-top: 1px solid #f0f0f0;
	}
	
	.cancel-btn,
	.confirm-btn {
		flex: 1;
		height: 100rpx;
		line-height: 100rpx;
		border: none;
		font-size: 32rpx;
	}
	
	.cancel-btn {
		background-color: #ffffff;
		color: #666666;
	}
	
	.confirm-btn {
		background-color: #ffffff;
		color: #1890ff;
		border-left: 1px solid #f0f0f0;
	}
	
	.empty-tip {
		text-align: center;
		padding: 120rpx 0;
		color: #999999;
	}
	
	.empty-tip text {
		display: block;
		font-size: 28rpx;
	}
	
	.sub-tip {
		font-size: 24rpx;
		margin-top: 20rpx;
	}
</style>
